let home = document.getElementById('home');
let bgClass = "--home-1";
home.classList.add(bgClass)

const setBackground = () => {
  home.classList.remove(bgClass)
  let bgNumber = parseInt(bgClass.replace(/^\D+/g, ''))
  if (bgNumber < 4) {
    bgNumber += 1
  } else {
    bgNumber = 1
  }

  bgClass = `--home-${bgNumber}`;
  home.classList.add(bgClass)
}

setInterval(setBackground, 3000);

